import React from 'react';
import { 
  Database, 
  GitBranch, 
  LineChart, 
  Brain, 
  ArrowRight,
  BarChart3,
  Sparkles
} from 'lucide-react';

const PipelineStep = ({ icon: Icon, title, description }: { 
  icon: React.ElementType;
  title: string;
  description: string;
}) => (
  <div className="flex flex-col items-center p-4">
    <div className="bg-primary/10 p-4 rounded-full">
      <Icon className="w-8 h-8 text-primary" />
    </div>
    <h3 className="mt-2 font-semibold">{title}</h3>
    <p className="text-sm text-muted-foreground text-center">{description}</p>
  </div>
);

const PipelineVisualization = () => {
  const steps = [
    {
      icon: Brain,
      title: "ABM Simulation",
      description: "Generate synthetic purchase data"
    },
    {
      icon: Database,
      title: "PostgreSQL",
      description: "Store transaction data"
    },
    {
      icon: GitBranch,
      title: "ETL with DBT",
      description: "Transform and model data"
    },
    {
      icon: BarChart3,
      title: "Analytics",
      description: "Process and analyze data"
    },
    {
      icon: Sparkles,
      title: "Dashboard",
      description: "Visualize insights"
    }
  ];

  return (
    <div className="w-full max-w-5xl mx-auto p-8">
      <div className="flex items-center justify-between">
        {steps.map((step, index) => (
          <React.Fragment key={step.title}>
            <PipelineStep {...step} />
            {index < steps.length - 1 && (
              <ArrowRight className="w-6 h-6 text-muted-foreground" />
            )}
          </React.Fragment>
        ))}
      </div>
    </div>
  );
};

export default PipelineVisualization; 